"use client"

import { createContext, useContext, useState, useCallback } from "react"

const EditorContext = createContext()

export function useEditor() {
  const context = useContext(EditorContext)
  if (!context) {
    throw new Error("useEditor must be used within EditorProvider")
  }
  return context
}

export function EditorProvider({ children }) {
  const [template, setTemplate] = useState(null)
  const [userPhoto, setUserPhoto] = useState(null)
  const [textFields, setTextFields] = useState({})
  const [photoPosition, setPhotoPosition] = useState({ x: 0, y: 0, scale: 1, rotation: 0 })
  const [activeTool, setActiveTool] = useState("upload")

  const updateTextField = useCallback((fieldId, value) => {
    setTextFields(prev => ({ ...prev, [fieldId]: value }))
  }, [])

  const updatePhotoPosition = useCallback((position) => {
    setPhotoPosition(prev => ({ ...prev, ...position }))
  }, [])

  const resetEditor = useCallback(() => {
    setTemplate(null)
    setUserPhoto(null)
    setTextFields({})
    setPhotoPosition({ x: 0, y: 0, scale: 1, rotation: 0 })
    setActiveTool("upload")
  }, [])

  const value = {
    template,
    setTemplate,
    userPhoto,
    setUserPhoto,
    textFields,
    updateTextField,
    photoPosition,
    updatePhotoPosition,
    activeTool,
    setActiveTool,
    resetEditor
  }

  return (
    <EditorContext.Provider value={value}>
      {children}
    </EditorContext.Provider>
  )
}
