"use client"

import { motion } from "framer-motion"
import { useEditor } from "@/contexts/editor-context"

export default function CanvasArea() {
  const { template, userPhoto, textFields, photoPosition } = useEditor()

  return (
    <div className="flex flex-1 items-center justify-center bg-muted p-8">
      <motion.div
        initial={{ scale: 0.95, opacity: 0 }}
        animate={{ scale: 1, opacity: 1 }}
        transition={{ duration: 0.4 }}
        className="relative flex h-full max-h-[600px] w-full max-w-[600px] items-center justify-center rounded-sm border-2 border-border bg-background shadow-lg overflow-hidden"
      >
        {template ? (
          <div className="relative w-full h-full">
            {/* Template Background */}
            <img 
              src={template.background} 
              alt={template.name}
              className="absolute inset-0 w-full h-full object-contain"
              onError={(e) => {
                e.target.style.display = 'none'
              }}
            />
            
            {/* Photo Area Indicator */}
            <div 
              className="absolute border-2 border-dashed border-blue-400 bg-blue-50/20"
              style={{
                left: `${template.photoArea.x}px`,
                top: `${template.photoArea.y}px`,
                width: `${template.photoArea.width}px`,
                height: `${template.photoArea.height}px`,
              }}
            />
            
            {/* User Photo */}
            {userPhoto && (
              <img
                src={userPhoto}
                alt="User"
                className="absolute object-cover"
                style={{
                  left: `${photoPosition.x}px`,
                  top: `${photoPosition.y}px`,
                  width: `${template.photoArea.width * photoPosition.scale}px`,
                  height: `${template.photoArea.height * photoPosition.scale}px`,
                  transform: `rotate(${photoPosition.rotation}deg)`,
                  transition: 'all 0.3s ease',
                }}
              />
            )}
            
            {/* Text Fields */}
            {template.textFields.map((field) => (
              <div
                key={field.id}
                className="absolute"
                style={{
                  left: `${field.x}px`,
                  top: `${field.y}px`,
                  fontSize: `${field.fontSize}px`,
                  fontWeight: field.fontWeight,
                  color: field.color,
                  fontFamily: field.fontFamily || 'inherit',
                }}
              >
                {textFields[field.id] || field.defaultValue}
              </div>
            ))}
          </div>
        ) : (
          <div className="flex flex-col items-center justify-center gap-4 p-8 text-center">
            <motion.div
              animate={{
                scale: [1, 1.05, 1],
              }}
              transition={{
                duration: 2,
                repeat: Number.POSITIVE_INFINITY,
                ease: "easeInOut",
              }}
              className="h-32 w-32 rounded-sm border-2 border-dashed border-border flex items-center justify-center"
            >
              <svg className="h-12 w-12 text-muted-foreground" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={1.5}
                  d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"
                />
              </svg>
            </motion.div>
            <div>
              <h3 className="text-lg font-serif font-semibold text-foreground mb-2">Your Canvas</h3>
              <p className="text-sm text-muted-foreground">Choose a template and upload a photo to get started</p>
            </div>
          </div>
        )}
      </motion.div>
    </div>
  )
}
